"use client";

import Highcharts from "highcharts";
import Highcharts3D from "highcharts/highcharts-3d";
import HighchartsReact from "highcharts-react-official";
import { useEffect } from "react";

Highcharts3D(Highcharts);

export default function AnalyticsDashboard() {
  useEffect(() => {
    Highcharts.setOptions({
      colors: [
        "#4CAF50", // green
        "#FFC107", // yellow
        "#F44336", // red
        "#03A9F4", // blue
        "#FF9800"  // orange
      ],
    });
  }, []);

  /* -------------------------- LINE CHART --------------------------- */
  const signInOptions = {
    chart: {
      type: "line",
      height: 350,
      marginTop: 50,
    },

    title: { text: "" },

    xAxis: {
      categories: ["01", "05", "10", "15", "20", "25", "30"],
      tickInterval: 1,
    },

    yAxis: {
      title: { text: "" },
      tickPositions: [0, 5, 10, 15, 20, 25, 30, 35, 40, 45],
      labels: {
        formatter() {
          return this.value + " M";
        },
      },
    },

    tooltip: {
      shared: true,
      backgroundColor: "black",
      borderRadius: 8,
      style: { color: "white" },
      formatter() {
        return (
          `<b>${this.points[0].key}</b><br/>` +
          this.points
            .map(
              (p) =>
                `<span style="color:${p.color}">\u25CF</span> ${p.series.name}: <b>${p.y}M</b><br/>`
            )
            .join("")
        );
      },
    },

    series: [
      {
        name: "July",
        data: [12, 16, 18, 22, 20, 19, 25],
        color: "black",
        lineWidth: 3,
      },
      {
        name: "June",
        data: [5, 7, 6, 10, 8, 9, 7],
        color: "gray",
        dashStyle: "Dash",
        lineWidth: 2,
      },
    ],
  };

  /* ------------------------ GENDER PIE ------------------------ */
  const genderOptions = {
    chart: {
      type: "pie",
      height: 300,
      options3d: { enabled: true, alpha: 45 },
    },
    title: { text: "" },
    plotOptions: {
      pie: {
        innerSize: 0,
        depth: 45,
        dataLabels: {
          enabled: true,
          format: "{point.name}: {point.y}%",
          style: { fontSize: "14px" },
        },
      },
    },
    series: [
      {
        data: [
          { name: "Male", y: 60, color: "#4CAF50" },
          { name: "Female", y: 30, color: "#FFEB3B" },
          { name: "Others", y: 10, color: "#F44336" },
        ],
      },
    ],
  };

  /* ------------------------ MATCHMAKING DONUT ------------------------ */
  const matchmakingOptions = {
    chart: {
      type: "pie",
      height: 300,
      options3d: { enabled: true, alpha: 45 },
    },

    title: { text: "" },

    plotOptions: {
      pie: {
        innerSize: 120,
        depth: 45,
        dataLabels: {
          enabled: true,
          format: "{point.name}<br>{point.y}%",
          style: { fontSize: "14px", fontWeight: "600" },
        },
      },
    },

    series: [
      {
        data: [
          { name: "Still Looking", y: 30, color: "#4CAF50" },
          { name: "Successfully Matched", y: 45, color: "#FF9800" },
          { name: "Newly Registered", y: 20, color: "#03A9F4" },
          { name: "Inactive", y: 5, color: "#FFC107" },
        ],
      },
    ],
  };

  /* ----------------------------- UI -------------------------------- */
  return (
    <div className="p-5 space-y-6">

      {/* ---------------- SIGN IN ANALYTICS ---------------- */}
      <div className="rounded-xl shadow p-5 bg-white">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-xl font-bold">Sign-In Analytics</h2>
            <div className="flex items-center gap-2 mt-2">
              <h1 className="text-3xl font-bold">23,000,000</h1>
              <span className="px-3 py-1 bg-green-100 text-green-600 rounded-full">
                ↑ 12%
              </span>
              <span className="text-green-600">Vs last Month</span>
            </div>
          </div>

          <div className="flex items-center gap-4 mr-5">
            <span className="flex items-center gap-2">
              <div className="w-3 h-3 bg-black rounded-full"></div> July
            </span>
            <span className="flex items-center gap-2">
              <div className="w-3 h-3 bg-gray-400 rounded-full"></div> June
            </span>
            <button className="border px-3 py-1 rounded-lg shadow-sm bg-gray-100">
              Month ▼
            </button>
          </div>
        </div>

        <HighchartsReact highcharts={Highcharts} options={signInOptions} />
      </div>

      {/* ----------------------- 2 CHARTS GRID ------------------------ */}
      <div className="grid grid-cols-2 gap-6">

        {/* ---------------- GENDER ANALYTICS ---------------- */}
        <div className="rounded-xl shadow p-5 bg-white">
          <div className="flex justify-between items-center">
            <h2 className="text-xl font-bold">Gender Analytics</h2>

            <div className="flex gap-4 text-sm">
              <span className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-green-500 rounded-sm"></div>
                Male
              </span>
              <span className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-yellow-500 rounded-sm"></div>
                Female
              </span>
              <span className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-red-500 rounded-sm"></div>
                Ots.
              </span>
            </div>
          </div>

          <HighchartsReact highcharts={Highcharts} options={genderOptions} />
        </div>

        {/* ---------------- MATCHMAKING STATUS ---------------- */}
        <div className="rounded-xl shadow p-5 bg-white">

          <div className="flex justify-between items-center">
            <h2 className="text-xl font-bold">Matchmaking Status</h2>

            <div className="text-sm space-y-2">
              <div className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-green-500 rounded-sm"></div>
                Still Looking
              </div>
              <div className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-orange-500 rounded-sm"></div>
                Successfully Matched
              </div>
              <div className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-blue-400 rounded-sm"></div>
                Newly Registered
              </div>
              <div className="flex items-center gap-1">
                <div className="w-4 h-4 border-2 border-yellow-500 rounded-sm"></div>
                Inactive
              </div>
            </div>
          </div>

          <HighchartsReact highcharts={Highcharts} options={matchmakingOptions} />

          <p className="text-center italic text-gray-600 mt-2">
            “Out Of 20M , 5M Is Matched”
          </p>
        </div>
      </div>
    </div>
  );
}
